<template>
  <div class="home">
    <Top></Top>
    <div class="main">
      <!-- <Aside></Aside> -->
      <Main></Main>
    </div>
    <Footer></Footer>
    <!--  -->
    <div class="info"><el-button class="info-btn" plain @click="open1">1</el-button></div>
    <div class="info"><el-button class="info-btn" plain @click="open2">2</el-button></div>
  </div>
</template>

<script>
import Top from '../components/Top.vue'
import Aside from '../components/Aside.vue'
import Main from '../components/Main.vue'
import Footer from '../components/Footer.vue'
export default {
  components: {
    Top,
    Aside,
    Main,
    Footer,
  },
  data() {
    return {
      isFlag: sessionStorage.getItem('UserId') ? true : false,
    }
  },
  methods: {
    open1() {
      const h = this.$createElement
      this.$notify.info({
        title: '',
        dangerouslyUseHTMLString: true,
        message: '<strong><br/><a style="color:rgb(252, 94, 136)" href="#/login"><i>/</i></a> </strong>',
        offset: 100,
      })
    },
    open2() {
      this.$notify({
        title: '',
        dangerouslyUseHTMLString: true,
        message: '<strong></strong>',
        type: 'success',
        offset: 100,
      })
    },
  },
  mounted() {
    if (this.isFlag) {
      this.open2()
    } else {
      this.open1()
    }
  },
  watch: {},
}
</script>

